import React from 'react';
import { connect } from 'react-redux';
import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import { getFAQ, createFaq, updateFaq, getFAQCategories} from "actions/FAQ";
import OrderTableCell from 'components/dashboard/eCommerce/OrderTableCell';
import IntlMessages from 'util/IntlMessages';
import { convertString } from "../../../shared/Methods";

class FAQ extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            faq: {
                title: "",
                content: "",
                faq_category: ""
            },
            required: false,
            submitText: 'submit'
        };

        this.toggle = this.toggle.bind(this);
    }


    componentDidMount(){
        this.props.getFAQCategories();
        this.props.getFAQ();
    }

     submit(params) {
        if(params === 'cancel'){
            this.toggle();
        }else if((params === 'submit') && (this.state.submitText === 'submit')){
            if(this.state.faq.title !== '' || !this.state.faq.content !== '' || !this.state.faq.faq_category !== ''){
              this.props.createFaq(this.state.faq);

            }else{
            this.setState({
                ...this.state,
                required: true
            })
            }
        }else if((params === 'submit') && (this.state.submitText === 'update')){
            if(!this.state.faq.title || !this.state.faq.content || this.state.faq.faq_category) {
                this.props.updateFaq(this.state.faq);
            }
        }

    }


    toggle(params, index, title){
        switch (params){
            case 'create':
                this.setState({
                    ...this.state,
                    modal: !this.state.modal,
                    faq : {
                        ...this.state.faq,
                        title: '',
                        content: '',
                        faq_id: '',
                        faq_category: ''
                    },
                    submitText: 'submit'
                });
                break;
            case 'edit':
                this.setState({
                    modal: !this.state.modal
                }, () => {
                    for (let property in this.props.faq) {
                        if (this.props.faq.hasOwnProperty(property)) {
                            if(property === title){
                                let editObject = this.props.faq[property][index];
                                this.setState({
                                    ...this.state,
                                    faq: {
                                        ...this.state.faq,
                                        faq_id: editObject.id,
                                        title: editObject.title,
                                        content: editObject.content,
                                        faq_category: editObject.faq_category
                                    },
                                    submitText: 'update'
                                })
                            }
                        }
                    }
                });
                break;
            default:
                this.setState({
                    modal: !this.state.modal
                });
        }

    }

    changeValue(e){
        this.setState({
            ...this.state,
            faq: {
                ...this.state.faq,
                [e.target.name]: e.target.value,
            },
            required: false

        }, () => {
            console.log(this.state.faq);
        })
    }





    render(){
        let keyObj = [];
        let contentObj = [];

         Object.keys(this.props.faq.faqItems).map((item) => {
             console.log(item);
           //  contentObj.push(this.props.faq.faqItems[item]);
             this.props.faq.faqItems[item].map((items) => {
                 contentObj.push(items);
                 if(keyObj.indexOf(items.faq_categories_title) === -1) {
                     keyObj.push(items.faq_categories_title);
                 }
             })

        });

        console.log(keyObj);
        console.log(contentObj);
        keyObj.map((item) => {


        });





        return (
            <div className={'faqMainContainer'}>
                <Button className="jr-btn btn-primary text-white mb-0" color="primary" onClick={() =>this.toggle('create', null, null)}>
                    <i className="zmdi zmdi-plus" style={{paddingRight: 5}}/>
                    <span>Create Faq</span>
                </Button>

                {keyObj.map((item, index) => {
                    return (
                        <div key={index} className={'container'}>
                            {contentObj.map((items, ind) => {

                                return (
                                    <div key={ind} className={'content'}>
                                        test
                                    </div>
                                );
                            })}
                        </div>
                    )
                })}

                <Modal isOpen={this.state.modal} toggle={() => this.toggle()} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Create Faq</ModalHeader>
                    <ModalBody>
                        {this.props.faq.faqCreationMessage !== {} ? <div className={'successCustom'}>{this.props.faq.faqCreationMessage.Message}</div> : ''}
                        {this.state.required ? <div className={'requiredField'}>All fields are required</div> : null}
                        <div className="form-group row">
                            <label htmlFor={'title'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Faq Category *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <input type="text" className="form-control" id="title"
                                       placeholder="Title" name={'title'} onChange={(event) =>this.changeValue(event)}
                                       value={this.state.faq.title}/>
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor={'content'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Content *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <textarea name={'content'}  onChange={(event) =>this.changeValue(event)} className="form-control"
                                          id="content" placeholder="Content" value={this.state.faq.content}/>
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor={'faqs'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Faq Category *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <select className="form-control" name={'faq_category'} onChange={(event) =>this.changeValue(event)}>
                                    <option value={''}>--Please Select--</option>
                                    {this.props.faq.faqCategories.map((data, index) => {
                                        return (
                                            <option value={data.id} key={data.id}>{data.title}</option>
                                        );
                                    })}
                                </select>
                            </div>
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="link" onClick={() =>this.submit('cancel')}> Cancel</Button>{' '}
                        <Button color="link" onClick={() => this.submit('submit')}>{this.state.submitText}</Button>
                    </ModalFooter>
                </Modal>


            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    faq: state.faq
});


export default connect(mapStateToProps, {getFAQ, createFaq, updateFaq, getFAQCategories})(FAQ);


